import React, { useState, useEffect } from 'react';
import uuid from 'react-uuid';

function convertProps(props) {
    if (props) {
        let keys = Object.keys(props);
        let mprops = [];
        if (keys.length) {
            keys.forEach(key => {
                mprops.push({
                    key: key,
                    value: props[key]
                })
            })
        }
        return mprops;
    } else return [];
}

const GroupTable = ({points, lines, areas, displayGid, masterProps, setMasterProps}) => {
    const [metaProps, setMetaProps] = useState(masterProps[displayGid] ? convertProps(masterProps[displayGid]) : []);
    const [showTable, toggleShowTable] = useState(true);
    const [groupName, setGroupName] = useState(masterProps[displayGid] && masterProps[displayGid]["Name"] ? masterProps[displayGid]["Name"] : displayGid);

    useEffect(() => {
        setMetaProps(masterProps[displayGid] ? convertProps(masterProps[displayGid]) : []);
        setGroupName(masterProps[displayGid] && masterProps[displayGid]["Name"] ? masterProps[displayGid]["Name"] : displayGid);
    }, [masterProps, displayGid]);

    return ( 
        <div>
            <div className="options" style={{marginBottom: 0, height: "35px"}}>
                <div style={{width: "100%", display: "flex", justifyContent: "space-between", alignItems: "baseline"}}>
                    <p style={{padding: "none"}}>Description</p>
                    <div style={{display: "flex", float: "right"}}>
                        <button className="group-button" style={{marginRight: "5px"}} onClick={() => {
                            toggleShowTable(!showTable);
                        }}>{showTable ? "Collapse" : "Expand"}</button>
                        <button className="group-button" style={{marginRight: "5px", backgroundColor: "rgb(0, 81, 255)"}} title="Add a field" onClick={() => {
                            if (!showTable) toggleShowTable(true);
                            setMetaProps([...metaProps, {
                                key: "",
                                value: ""
                            }]);
                        }}>Add</button>
                        <button className="group-button" style={{marginRight: "5px", backgroundColor: "rgb(4, 142, 9)"}} title="Confirm properties" onClick={() => {
                            let modProps = {};
                            for (let prop of metaProps) {
                                if (prop["key"]) modProps[prop["key"]] = prop["value"]
                            }
                            let newMasterProps = {...masterProps};
                            newMasterProps[displayGid] = modProps;
                            if (groupName !== displayGid) {
                                newMasterProps[displayGid]["Name"] = groupName;
                            }
                            setMasterProps(newMasterProps);
                        }}>Set</button>
                        <button className="group-button" style={{backgroundColor: "rgb(231, 7, 7)"}} title="Clear group properties" onClick={() => {
                            let newMasterProps = {...masterProps};
                            newMasterProps[displayGid] = {};
                            setMasterProps(newMasterProps);
                            setGroupName(displayGid);
                        }}>Clear</button>
                    </div>
                </div>
            </div>
            {showTable ? (
                <div className="options" style={{marginTop: "-5px"}}>
                    <table className="gedit">
                        <thead>
                            <tr>
                                <td>Name</td>
                                <td id="gname" style={{color: "rgb(9, 18, 99)"}} contentEditable="true" suppressContentEditableWarning="true" onBlur={(e) => {
                                    setGroupName(document.getElementById("gname").innerText);
                                }}>{groupName}</td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>Points</td>
                                <td>{points.filter(point => point.gid === displayGid).length}</td>
                            </tr>
                            <tr>
                                <td>Lines</td>
                                <td>{lines.filter(line => line.gid === displayGid).length}</td>
                            </tr>
                            <tr>
                                <td>Areas</td>
                                <td>{areas.filter(area => area.gid === displayGid).length}</td>
                            </tr>
                            {masterProps[displayGid] && metaProps.map(mprop => (
                                mprop["key"] !== "Name" ? (
                                    <tr key={uuid()}>
                                        <td id={`meta_${metaProps.indexOf(mprop)}`} contentEditable="true" suppressContentEditableWarning="true" onBlur={(e) => {
                                            mprop["key"] = document.getElementById(`meta_${metaProps.indexOf(mprop)}`).innerText;
                                        }}>{mprop["key"]}</td>
                                        <td id={`val_${metaProps.indexOf(mprop)}`} contentEditable="true" suppressContentEditableWarning="true" onBlur={(e) => {
                                            mprop["value"] = document.getElementById(`val_${metaProps.indexOf(mprop)}`).innerText;
                                        }}>{mprop["value"]}</td>
                                    </tr>
                                ) : (null)
                            ))}
                        </tbody>
                    </table>
                </div>
            ) : (null)}
        </div>
    );
}
 
export default GroupTable;