import React, { PureComponent } from 'react';
import uuid from 'react-uuid';
import CloseIcon from '../graphics/close.svg';


class ListPoints extends PureComponent {
    state = {
        page: 1
    }
    paginate = (selectedPoints, page) => {
        let numPages = Math.ceil(selectedPoints.length / 20);
        if (page < numPages) return selectedPoints.slice((page - 1) * 20, page * 20);
        else return selectedPoints.slice((page - 1) * 20);
    }
    getPageArray = (selectedPoints) => {
        let numPages = Math.ceil(selectedPoints.length / 20);
        let pageArray = [];
        for (let i = 1; i <= numPages; i++) {
            pageArray.push(i);
        }
        return pageArray
    }
    render() {
        const points = this.props.points;
        const setPoints = this.props.setPoints;
        const displayGid = this.props.displayGid;
        let selectedPoints = displayGid ? points.filter(point => point.gid === displayGid) : points;
        return ( 
            <div>
                <div className="dlist">
                    {selectedPoints.length ? (
                        this.paginate(selectedPoints, this.state.page).map((point) => (
                            <div key={uuid()} style={{color: "blue", display: "flex", alignItems: "baseline"}}>
                                <p>{`${selectedPoints.indexOf(point)+1}.  [${point.coordinates[1].toFixed(8)}, ${point.coordinates[0].toFixed(8)}]`}</p>
                                <button className="blank" title="Clear" onClick={() => {
                                    let index = points.indexOf(point);
                                    let newPoints = points.slice();
                                    if (index > -1) {
                                        newPoints.splice(index, 1);
                                        setPoints(newPoints);
                                    }
                                }}><img src={CloseIcon} alt="close" width="12px"></img></button>
                            </div> 
                        )) 
                    ) : (null)}
                </div>
                {this.getPageArray(selectedPoints).length > 1 ? (
                    <div className="paginate">
                        {this.getPageArray(selectedPoints).map(p => (
                            <span className={p === this.state.page ? "cspan" : "rspan"} onClick={() => {
                                this.setState({...this.state, page: p});
                            }}>{p}</span>
                        ))}
                    </div>
                ) : (null)}
            </div>
         );
    }
}
 
export default ListPoints;