import React, { PureComponent } from 'react'
import uuid from 'react-uuid';
import Entity from './Entity';

class ListEntities extends PureComponent {
    state = {
        page: 1
    }
    paginate = (selectedPoints, page) => {
        let numPages = Math.ceil(selectedPoints.length / 20);
        if (page < numPages) return selectedPoints.slice((page - 1) * 20, page * 20);
        else return selectedPoints.slice((page - 1) * 20);
    }
    getPageArray = (selectedPoints) => {
        let numPages = Math.ceil(selectedPoints.length / 20);
        let pageArray = [];
        for (let i = 1; i <= numPages; i++) {
            pageArray.push(i);
        }
        return pageArray
    }
    render() { 
        const entities = this.props.entities;
        const setEntities = this.props.setEntities;
        const type = this.props.type;
        const displayGid = this.props.displayGid;
        const selectedEntities = displayGid ? entities.filter(ent => ent.gid === displayGid) : entities;
        return ( 
            <div>
                <div className="dlist">
                    {this.paginate(selectedEntities, this.state.page).map(ent => (
                        <Entity key={uuid()} type={type} entities={entities} setEntities={setEntities} ent={ent} />
                    ))}
                </div>
                {this.getPageArray(selectedEntities).length > 1 ? (
                    <div className="paginate" style={{marginTop: "5px", marginBottom: "5px", height: "30px"}}>
                        {this.getPageArray(selectedEntities).map(p => (
                            <span className={p === this.state.page ? "cspan" : "rspan"} onClick={() => {
                                this.setState({...this.state, page: p});
                            }}>{p}</span>
                        ))}
                    </div>
                ) : (null)}
            </div>
         );
    }
}
 
export default ListEntities;